//Partial File: Footer

@use 'variables' as *;


.footer {
    border-top-color: $text-color-yellow;
    background-color: $body-color;
    padding-bottom: 1rem;
    border-top: 0.10rem solid $text-color-yellow;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.footer-links ul {
    list-style-type: none;
    padding: 0;
    margin: 1rem auto;
    display: flex;
}

.footer-links li {
    margin: auto 0.5rem;
}

.footer-full-logo {
    display: none;
}

@media (min-width: 600px) {
    .footer {
        flex-direction: row;
        align-items: flex-end;
    }

    .footer-links {
        position: absolute;
        bottom: 1rem;
        right: 1rem;
    }

    .footer-mini-logo {
        margin: 0.75rem 0 0 1rem;
    }
}

@media (min-width: 62.5em) {
    .footer-mini-logo {
        display: none;
    }

    .footer-full-logo {
        display: block;
        margin: 1.5rem 1.5rem 0.5rem 1.5rem;
    }

    .footer-links {
        bottom: 2.25rem;
        right: 2.25rem;
    }
}